@import url(https://fonts.googleapis.com/css?family=Raleway:100,200,300,400,500);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400);
@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App,
html,
body,
.input {
  background: #2a2734;
  font-family: 'Roboto', sans-serif !important; }

* {
  text-align: center; }

.inputWrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100%; }
  .inputWrapper .header {
    font-weight: 100;
    width: 100%;
    line-height: 150px;
    margin: 0;
    text-align: center;
    font-size: 55px;
    color: #9b87fd; }
    .inputWrapper .header span {
      font-family: 'Roboto', sans-serif;
      font-weight: 100; }
  .inputWrapper .description {
    color: white;
    font-size: 20px;
    font-weight: 300;
    margin: 0; }
    .inputWrapper .description .info {
      padding: 5px;
      padding-right: 8px; }
      .inputWrapper .description .info svg {
        margin-bottom: -4px !important;
        color: #c5bbf5; }
      .inputWrapper .description .info i {
        font-size: 20px; }
  .inputWrapper .input {
    font-family: 'Roboto', sans-serif !important;
    width: 100%;
    height: 120px;
    border: none;
    overflow: auto;
    outline: none;
    text-align: center;
    color: white;
    font-size: 80px;
    font-weight: 200; }
  .inputWrapper .result {
    font-size: 80px;
    font-weight: 200;
    margin: 0;
    color: #b2a4f7;
    text-align: center; }

input[type='number']::-webkit-inner-spin-button,
input[type='number']::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0; }

